import React, { Component } from 'react';
import { VelocityTransitionGroup } from 'velocity-react';

import Top from './Top';
import About from './About';
import Works from './Works';
import ItemOfWorks from './ItemOfWorks';
import Researches from './Researches';

class Content extends Component {
    render() {
        let content = [];
        //velocity-reactkeyid
        if(this.props.content === 'initial')content.push(<Top key={0} />)
        else if(this.props.content === 'works')content.push(<Works linkTo={this.props.linkTo} payload={this.props.payload} key={1} />)
        else if(this.props.content === 'about')content.push(<About key={2} />)
        else if(this.props.content === 'itemOfWorks')content.push(<ItemOfWorks payload={this.props.payload} key={3} />)
        else if(this.props.content === 'researches')content.push(<Researches palyload={this.props.payload} key={4} />)

        return (
            <VelocityTransitionGroup
                runOnMount={true}
                enter={
                    {
                        animation: 'fadeIn',
                        stagger: 100,
                    }
                }
            >
                {content}
            </VelocityTransitionGroup>
        );
    }
}
export default Content;


