import React, { Component } from 'react';
import {
	Card, CardImg, CardText, CardBody,
	CardTitle, CardSubtitle, Button, Col, Row, Container
} from 'reactstrap';

class Works extends Component {

	constructor(props) {
		super(props);
		this.state = {
			works: []
		}
		fetch("https://shoponpon.github.io/works.json").then((response) => {
			response.json().then((json) => {
				this.setState(json);
			});
		});
	}

	renderItem(index) {

		const works = this.state.works;

		const description = works[index].description.length >= 30 ?
			works[index].description.substr(0, 49) + "" :
			works[index].description;
		const iconUrl = works[index].photo_urls.length != 0 ? works[index].photo_urls[0] : "https://placeholdit.imgix.net/~text?txtsize=33&txt=no_image&w=318&h=180";

		return (
			<Col xs="12" sm="4" style={styles.col} key={index}>
				<Card style={styles.card}>
					<CardImg top style={styles.cardImage} src={iconUrl} alt="work image" />
					<CardBody>
						<CardTitle>{works[index].title}</CardTitle>
						<CardSubtitle>{works[index].subtitle}</CardSubtitle>
						<div style={styles.keywords}>
							{(() => {
								let keywords = []
								for (let i = 0; i < works[index].keywords.length; i++) {
									keywords.push(<small style={styles.keyword}>{works[index].keywords[i]}</small>);
								}
								return keywords;
							})()}
						</div>
						<CardText>{description}</CardText>
						<Button onClick={() => this.props.linkTo('itemOfWorks', works[index])} style={styles.button}></Button>
					</CardBody>
				</Card>
			</Col>
		);
	}

	render() {

		const styles = {
			title: {
				fontWeight: 'bold'
			}
		}

		console.log(this.state.works);
		return (
			<div>
			<div>
				<p style={styles.title}>(2017)</p>
				<ul>
					<li></li>
					<li>Fringe8110</li>
					<li></li>
					<li>NTT</li>
					<li></li>
					<li>VOYAGE GROUP</li>
				</ul>
			</div>
			<p style={styles.title}></p>
			<p></p>
			<Row>
				{(() => {
					let works = []
					for (let i = 0; i < this.state.works.length; i++) {
						works.push(this.renderItem(i));
					}
					return works;
				})()}
			</Row>
			</div >
		);
	}
}

const styles = {
	col: {
		padding: 5,
		color: 'black'
	},
	card: {
		border: 'solid',
		borderWidth: 1,
		borderColor: "white",
		margin: 5
	},
	cardImage: {
		width: '100%',
		height: 200,
		objectFit: 'cover'
	},
	button: {
		margin: 2,
		color: '#343a40',
		borderColor: '#343a40',
		backgroundColor: "transparent"
	},
	keyword: {
		padding: 1,
		margin: 1,
		border: 'solid',
		borderWidth: 1,
		borderColor: '#343a40',
		borderRadius: 10,
		backgroundColor: '#343a40',
		color: 'white'
	}
}

export default Works;



