import React from 'react';
import {
    Col,
    Row,
    Carousel,
    CarouselItem,
    CarouselControl,
    CarouselIndicators,
    CarouselCaption
} from 'reactstrap';
import './ItemOfWorks.css';

export default class ItemOfWorks extends React.Component {

    constructor(props) {
        super(props);
        this.state = { activeIndex: 0 };
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
        this.goToIndex = this.goToIndex.bind(this);
        this.onExiting = this.onExiting.bind(this);
        this.onExited = this.onExited.bind(this);
    }

    onExiting() {
        this.animating = true;
    }

    onExited() {
        this.animating = false;
    }

    next() {
        if (this.animating) return;
        if (this.state.activeIndex+1 < this.props.payload.photo_urls.length){
            const nextIndex = this.state.activeIndex + 1;
            this.setState({ activeIndex: nextIndex });
        }
    }

    previous() {
        if (this.animating) return;
        if (this.state.activeIndex-1 >= 0){
            const nextIndex = this.state.activeIndex - 1;
            this.setState({ activeIndex: nextIndex });
        }
    }

    goToIndex(newIndex) {
        if (this.animating) return;
        this.setState({ activeIndex: newIndex });
    }

    render() {

        const { title, subtitle, description, keywords, photo_urls, related_urls, slide_url } = this.props.payload;

        const { activeIndex } = this.state;

        const slides = photo_urls.map((item) => {
            return (
                <CarouselItem
                    onExiting={this.onExiting}
                    onExited={this.onExited}
                    key={item}
                >
                    <img style={styles.image} src={item} />
                </CarouselItem>
            );
        });

        const urls = related_urls.map((item) => {
            return(
                <a style={styles.url} href={item} target="_blank">{item}</a>
            );
        });

        return (
            <div>
                <h1>
                    {title}<br className="br-sp" />
                    <br/><small><small style={styles.subtitle}>{subtitle}</small></small>
                </h1>
                <div style={styles.keywords}>
                    <p>{keywords.join(', ')}</p>
                </div>
                <Row>
                    <Col sm="8" xs="12">
                        <Carousel
                            activeIndex={activeIndex}
                            next={this.next}
                            previous={this.previous}
                        >
                            <CarouselIndicators items={photo_urls} activeIndex={activeIndex} onClickHandler={this.goToIndex} />
                            {slides}
                            <CarouselControl direction="prev" directionText="Previous" onClickHandler={this.previous} />
                            <CarouselControl direction="next" directionText="Next" onClickHandler={this.next} />
                        </Carousel>
                    </Col>
                    <Col sm="4" xs="12">
                        <h3></h3>
                        <p>{description}</p>
                    </Col>
                </Row>
                <h3></h3>
                    <object id="slide" data={slide_url} type="application/pdf" width="800" height="500">
                        <p>PDF</p>
                        <p><a href="resources/pdf/tomoila.pdf">PDF</a></p>
                    </object>
                <h3>URL</h3>
                {urls}
            </div>
        );
    }
}

const styles = {
    image: {
        width: 300,
    },
    subtitle: {
        color: '#ababab'
    },
    url: {
        margin: 5
    }
}


