import React from 'react';
import {
  Button,
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container 
} from 'reactstrap';

import Content from './Content';

export default class Navigation extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false,
      current: 'initial',
      payload: {},
    };
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  linkTo(pageName,payload) {
    this.setState({
      current: pageName,
      payload: payload,
    });
  }

  render() {
    return (
      <div>
        <Navbar color="faded" light expand="md">
          <NavbarToggler onClick={this.toggle} style={styles.toggler} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar style={styles.buttons}>
              <NavItem>
                <Button style={styles.button} outline onClick={() => this.linkTo("about")} >About</Button>
              </NavItem>
              <NavItem>
                <Button style={styles.button} outline onClick={() => this.linkTo("works")} >Works</Button>
              </NavItem>
              <NavItem>
                <Button style={styles.button} outline onClick={() => this.linkTo("researches")} >Researches</Button>
              </NavItem>
              <NavItem>
                <a href="https://github.com/shoponpon" target="_blank">
                  <Button style={styles.button} outline onClick={() => {}}>GitHub</Button>
                </a>
              </NavItem>
              <NavItem>
                <a href="https://pixiv.me/shoponpon" target="_blank">
                  <Button style={styles.button} outline onClick={() => {}}>Illustrations</Button>
                </a>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
        <Container>
          <Content content={this.state.current} payload={this.state.payload} linkTo={this.linkTo.bind(this)} />
        </Container>
      </div>
    );
  }
}

const styles = {
  buttons: {
    flex: 1,
    textAlign: 'center',
    justifyContent: 'center'
  },
  button: {
    margin: 5,
    color: 'white',
    borderColor: 'white',
    backgroundColor: "transparent"
  },
  toggler: {
    backgroundColor: 'white'
  }
}


