import React, { Component } from 'react';

export default class Top extends Component {
    render() {
        return (
            <div style={styles.logoArea}>
                <h1 style={styles.logo}>Portfolio</h1>
            </div>
        );
    }
}

const styles = {
    logoArea: {
        justifyContent: 'center',
        textAlign: 'center'
    },
    logo: {
        fontWeight: 'bold',
        letterSpacing: 10
    }
};


